import * as React from "react";

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepLabel from "@mui/material/StepLabel";
import Typography from "@mui/material/Typography";
import Wlakin1 from "./Walkin1";
import Walkin2 from "./Walkin2";
import Walkin3 from "./Walkin3";
import Walkin4 from "./Walkin4";
import Walkin5 from "./Walkin5";

import Paper from "@mui/material/Paper";
import Container from "@mui/material/Container";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";

const steps = [
  "เลือกห้องและวัน",
  "กรอกรายละเอียด",
  "รายละเอียด",
  "ชำระเงิน",
  "เสร็จสิ้น",
];
const stepContentMap = {
  0: <Wlakin1 />,
  1: <Walkin2 />,
  2: <Walkin3 />,
  3: <Walkin4 />,
  4: <Walkin5 />,
};
export default function Userdata() {
  const { atId } = useParams();
  const navigate = useNavigate();

  const handleNext = () => {
    // setActiveStep(activeStep + 1);
    navigate(`/dashboard/walk_check_in/5/${parseInt(atId) + 1}`);
  };

  const handleBack = () => {
    // setActiveStep(activeStep - 1);
    navigate(`/dashboard/walk_check_in/5/${parseInt(atId) - 1}`);
  };

  return (
    <React.Fragment>
      <Container sx={{ py: 1 }} maxWidth="md">
        <Container component="main" maxWidth="xl" sx={{ mb: 4 }}>
          <Paper
            variant="outlined"
            sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}
          >
            <Typography component="h1" variant="h4" align="center">
              ระบบจองห้อง
            </Typography>
            <Stepper activeStep={parseInt(atId)} sx={{ pt: 3, pb: 5 }}>
              {steps.map((label) => (
                <Step key={label}>
                  <StepLabel>{label}</StepLabel>
                </Step>
              ))}
            </Stepper>
            {parseInt(atId) === steps.length ? (
              <React.Fragment>
                <Typography variant="h5" gutterBottom>
                  Thank you for your order.
                </Typography>
                <Typography variant="subtitle1">
                  Your order number is #2001539. We have emailed your order
                  confirmation, and will send you an update when your order has
                  shipped.
                </Typography>
              </React.Fragment>
            ) : (
              <React.Fragment>
                {stepContentMap[atId]}
                <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                  {parseInt(atId) !== 0 && (
                    <Button onClick={handleBack} sx={{ mt: 3, ml: 1 }}>
                      Back
                    </Button>
                  )}
                  {parseInt(atId) >= 2 && (
                    <Button
                      variant="contained"
                      onClick={handleNext}
                      sx={{ mt: 3, ml: 1 }}
                    >
                      {parseInt(atId) === steps.length - 1
                        ? "เสร็จสิ้น"
                        : "Next"}
                    </Button>
                  )}
                </Box>
              </React.Fragment>
            )}
          </Paper>
        </Container>
      </Container>
    </React.Fragment>
  );
}
